/*Blinker css*/

@-webkit-keyframes blinker {  
  0% { opacity: 1; }
  49% { opacity: 1; }
  50% { opacity: 0; }
  100% { opacity: 0; }
}

@keyframes blinker {  
  0% { opacity: 1; }
  49% { opacity: 1; }
  50% { opacity: 0; }
  100% { opacity: 0; }
}

/*Desktop blinker css*/

.scrabble-input-box .scrabble-tiles.blinker:last-child:after {
	content: "";
	position: absolute;
	background-color: white;
	margin-left: 5px;
	width: 3px;
	height: 60px;
	-webkit-animation: blinker 1s linear infinite;
	        animation: blinker 1s linear infinite;
}

.scrabble-input-box.large .scrabble-tiles.blinker:last-child:after {
	top: 68px;
}

/*Mobile blinker css*/

.mobile .scrabble-tiles.blinker:last-child:after {
	width: 2px;
	height: 44px;
}

.mobile .scrabble-input-box.large .scrabble-tiles.blinker:last-child:after {
	top: 54px;
	width: 2px;
	height: 44px;
}





/*Modifier css*/

.modifier {
	color: black;
	margin: 2px;
	background-image: url("/modifier-frame.png");
	background-size: 138px 69px;
	width: 69px;
	height: 69px;
	display: inline-block;
	text-align: center;
	font-family: sans-serif;
	line-height: 120%;
	padding-top: 13px;
